import { lazy } from "react";
import { Navigate, Route, Routes } from "react-router-dom";
// import { MovieDetails } from "pages/MovieDetails";
// import { Home } from "../pages/Home";
// import { Movies } from "../pages/Movies";
import { Cast } from "./Cast";
import { Reviews } from "./Reviews";
import { SharedLayout } from "./SharedLayout";

const Home = lazy(() => import("../pages/Home"));
const Movies = lazy(() => import("../pages/Movies"));
const MovieDetails = lazy(() => import("../pages/MovieDetails"));

export const App = () => {
  return (
    <Routes>
      <Route path="/" element={<SharedLayout />} >
        <Route index element={<Home />} />
        <Route path="/movies" element={<Movies />} />
        <Route path="/movies/:movieId" element={<MovieDetails />} >
          <Route exact path="/movies/:movieId/cast" element={<Cast />} />
          <Route exact path="/movies/:movieId/reviews" element={<Reviews />} />
        </Route>
        <Route path="*" element={<Navigate to="/" />} />
      </Route>
    </Routes>
  );
};

// '/' -  Home,      .
// '/movies' -  Movies,      .
// '/movies/:movieId' -  MovieDetails,      .
// /movies/:movieId/cast -  Cast,    .    MovieDetails.
// /movies/:movieId/reviews -  Reviews,   .    MovieDetails.
